html {
  overflow-x: hidden;
}

.Hero {
  height: 80vh;
  position: relative;
  display: flex;
  -webkit-animation: carousel 10s infinite forwards;
          animation: carousel 10s infinite forwards;
}

@-webkit-keyframes carousel {
  0% {
    opacity: 0;
    -webkit-transform: translateX(0vh);
            transform: translateX(0vh);
  }
  16% {
    opacity: 1;
    -webkit-transform: translateX(0vh);
            transform: translateX(0vh);
  }
  33% {
    -webkit-transform: translateX(-175.5vh);
            transform: translateX(-175.5vh);
    opacity: 1;
  }
  50% {
    -webkit-transform: translateX(-175.5vh);
            transform: translateX(-175.5vh);
  }
  67% {
    -webkit-transform: translateX(-351vh);
            transform: translateX(-351vh);
    opacity: 1;
  }
  83% {
    -webkit-transform: translateX(-351vh);
            transform: translateX(-351vh);
  }
  100% {
    opacity: 0;
    -webkit-transform: translateX(-351vh);
            transform: translateX(-351vh);
  }
}

@keyframes carousel {
  0% {
    opacity: 0;
    -webkit-transform: translateX(0vh);
            transform: translateX(0vh);
  }
  16% {
    opacity: 1;
    -webkit-transform: translateX(0vh);
            transform: translateX(0vh);
  }
  33% {
    -webkit-transform: translateX(-175.5vh);
            transform: translateX(-175.5vh);
    opacity: 1;
  }
  50% {
    -webkit-transform: translateX(-175.5vh);
            transform: translateX(-175.5vh);
  }
  67% {
    -webkit-transform: translateX(-351vh);
            transform: translateX(-351vh);
    opacity: 1;
  }
  83% {
    -webkit-transform: translateX(-351vh);
            transform: translateX(-351vh);
  }
  100% {
    opacity: 0;
    -webkit-transform: translateX(-351vh);
            transform: translateX(-351vh);
  }
}

