.History {
    position: relative;
    height: 100vh;
    width: 100vw;
    display: grid;
    grid-template-areas: 'start' 'team';
    grid-auto-rows: 50vh 50vh;
    font-family: 'Playfair Display', serif;
    font-weight: 100;
}

.History__start {
    grid-area: start;
    display: grid;
    grid-template: 'title text';
    grid-auto-columns: 50vw 50vw;
}

.History__team {
    grid-area: team;
    display: grid;
    grid-template: 'text title';
    grid-auto-columns: 50vw 50vw;
}

.History__start__title {
    grid-area: title;
    background-image: -webkit-gradient(linear, left top, right top, from(#F5C36C), to(white));
    background-image: -webkit-linear-gradient(left, #F5C36C, white);
    background-image: linear-gradient(to right, #F5C36C, white)
}

.History__start__text__container {
    grid-area: text;
}

.History__team__title {
    grid-area: title;
    background-image: -webkit-gradient(linear, right top, left top, from(#F5C36C), to(white));
    background-image: -webkit-linear-gradient(right, #F5C36C, white);
    background-image: linear-gradient(to left, #F5C36C, white)
}

.History__team__text {
    grid-area: text;
    display: flex;
    align-items: center;
}

.History__title {
    margin: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 3em;
}

.History__text {
    font-size: 1.2em;
    text-indent: 3em;
    padding: 30px;
    margin: 0;
