/* import to icons */
@font-face {
    font-family: 'icomoon';
    src: url(/volpolini/static/media/icomoon.dd6894a5.eot);
    src: url(/volpolini/static/media/icomoon.dd6894a5.eot#iefix) format('embedded-opentype'), url(/volpolini/static/media/icomoon.21c00c34.ttf) format('truetype'), url(/volpolini/static/media/icomoon.f6dc0acf.woff) format('woff'), url(/volpolini/static/media/icomoon.87de5da0.svg#icomoon) format('svg');
    font-weight: normal;
    font-style: normal;
}

[class^="icon-"], [class*=" icon-"] {
    /* use !important to prevent issues with browser extensions that change fonts */
    font-family: 'icomoon' !important;
    speak: none;
    font-style: normal;
    font-weight: normal;
    -webkit-font-feature-settings: normal;
            font-feature-settings: normal;
    font-variant: normal;
    text-transform: none;
    line-height: 1;
    /* Better Font Rendering =========== */
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

.icon-facebook2:before {
    content: "\EA91";
}

.icon-instagram:before {
    content: "\EA92";
}

